
.lomasbuscado {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: 6px;
}

.lomasbuscado h4 {
    font-style: normal;
    font-weight: 800;
    font-size: 27px;
    line-height: 30px;
    
    width: 48%;
    margin: 6px;
    
}

.lomasbuscado .articulo {
    display: flex;
    justify-content: center;
    align-items: center;
    
    width: 48%;
}

.lomasbuscado .articulo p {
    font-size: 0.8rem;
}





@media (min-width: 700px) {

    .lomasbuscado {
        margin-top: 80px;
        margin-bottom: 80px;
    }
    .lomasbuscado h4 {
        
        
        width: 15%;
        
        
    }
    
    .lomasbuscado .articulo {
        
        width: 15%;
    }
}