

.cuotas {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 30px;
}


.cuotas .tarjeta {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cuotas .tarjeta img {
    margin-right: 20px;
}

.numerocuotas {
    display: flex;
    flex-direction: column;
}

.numerocuotas h2 {
    margin-bottom: 0px;
}

.numerocuotas h3 {
    margin-top: 0px;
}

.numerocuotas .subtitle {
    color: #EC2227;;
}

.contenedor-flecha {
    width: 24.48px;
    height: 24px;
    background: #FFFFFF;
    border: 1px solid #F0F0F0;
    border-radius: 24px;

    text-align: center;
}


/* Desktop */
.cuotas-desktop {
    display: none;
    
}

.cuotas-desktop .separador {
    margin: 10px;
    width: 36px;
    height: 0px;
    border: 1px solid #C8C8C8;
    transform: rotate(-60deg);
}


.cuotas-desktop .tarjeta {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cuotas-desktop .tarjeta img {
    margin-right: 20px;
}




@media (min-width: 700px) {
    .cuotas-desktop {
        order: -1;
        margin-top: 0px;
        margin-bottom: 40px;
        margin-left: 50px;
        margin-right: 50px;

        
    }

    .cuotas .contenedor-flecha {
        display: none;
    }
    
    .cuotas .carousel-mobile {
        display: none;
    }

    .cuotas-desktop {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;
    }
}