
.nuestrasmarcas {
    margin-top: 50px;
    
}

.nuestrasmarcas h1 {
    text-align: center;
}

.image-carousel {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.image-carousel .box-shadow {
    padding: 10px;
    background: #FFFFFF;
    box-shadow: 0px 3px 9px rgba(0, 0, 0, 0.15);
    border-radius: 3px;
}

.image-carousel button {
    width: 24.48px;
    height: 24px;
    background: #FFFFFF;
    border: 1px solid #F0F0F0;
    border-radius: 24px;

    text-align: center;
}

.image-carousel .side {
    opacity: 0.3;
}



@media (max-width: 380px){
    .nuestrasmarcas {
    width: 100%;
    overflow: hidden;
    margin: auto;
    }

    .image-carousel .box-shadow {
        margin: 0 20px;
    }

    .image-carousel {
        width: 90%;
        overflow: hidden;
        margin: auto;
    }

    .image-carousel button {
        position: relative;
        
        z-index: 1;
    }

    .image-carousel .left {
        left: 100px;
    }
    
    .image-carousel .right {
        right: 100px;
    }
}