

.novedades .top {
    margin: 12px;
}
.novedades h4 {
    margin-bottom: 15px;
    font-weight: 800;
    font-size: 27px;
    line-height: 30px;

}

.novedades h5 {

    margin-top: 0px;

    font-style: normal;
    font-weight: 800;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

}

.novedades .products {
    display: flex;
    direction: row;
    justify-content: space-around;
    align-items: center;
    
    
}

.novedades .products .card {
    width: 49%;
}

.novedades .paginacion {
    display: flex;
    justify-content: space-between;

    margin-top: 0px;
}

.novedades .paginacion p {
    margin-right: 15px;
}

.novedades .paginacion .flechas {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.novedades .flecha {
    margin-left: 20px;

    width: 48px;
    height: 48px;
    left: 0px;
    top: calc(50% - 48px/2);
    background: #FFFFFF;
    border: 1px solid #C8C8C8;
    border-radius: 24px;

    display: flex;
    justify-content: center;
    align-items: center;
}


/* Novedades Desktop */

.novedades .products-desktop {
    display: none;
}

.novedades .products-desktop-display {
    display: none;
}




@media (min-width: 700px) {

    .novedades {
        margin-top:0px;
        margin-bottom: 50px;
    }

    .novedades .products-mobile {
        display: none;
    }

    .novedades h4 {
        display:inline-block;
    }

    .novedades h5 {
        display:inline-block;
        margin-left: 40px;
    }

    .novedades .products-desktop {
        display: flex;
    }

    .novedades .products-desktop-display {
        display: flex;
    }

    .novedades .products-desktop .card {
        width: 24%;
    }
}