

.lomasbuscado .contador {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    
}

.lomasbuscado .contador h5 {
    margin: 0px;
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.05em;
    font-feature-settings: 'ss01' on, 'case' on;
    color: #505050;
}

.lomasbuscado .contador .hora {
    font-style: normal;
font-weight: 800;
font-size: 25px;
line-height: 30px;
}

.lomasbuscado .contador p {
    margin: 0px;
}

.lomasbuscado .contador .hs {
    display: flex;
    justify-content: space-between;
}

.lomasbuscado .contador .hs span {
    margin-right: 5px;
}