
.productlist {
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.productlist .contenedor-filtros {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.productlist .contenedor-filtros button {
    border: none;
    background: none;
}

.productlist .ruta {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #8C8C8C;

    margin-top: 40px;
    margin-bottom: 30px;
}

.productlist .contenedor-filtros .filtrar {
    width: 270px;
    border: solid 2px #AAAAAA;
    border-radius: 4px;
}

.productlist .contenedor-filtros .filtrar select {
    border: none;
    background: none;
    padding: 8px;
    width: 260px;
}

.productlist .contenedor-filtros .ordenar {
    width: 270px;
    border: solid 2px #F0F0F0;
    border-radius: 4px;
}

.productlist .contenedor-filtros .ordenar select {
    border: none;
    background: none;
    padding: 8px;
    width: 260px;
}

.productlist .filtrar {
    margin-bottom: 10px;
}

.productlist .ordenar {
    margin-bottom: 50px;
}

.productlist .ruta strong {
    color: #000000;
    
}

.productlist .banner {
    width: 100vw;
    margin-bottom: 35px;
    
}

.productlist .productos-horizontal {
    width: 90vw;
}

.productlist .productos-vertical {
    display: none;
    width: 100%;
}

.productlist .paginado {
    margin-top: 50px;
    margin-bottom: 35px;
}





@media (min-width: 700px) {
    .productlist .banner {
        margin-top: 40px;
        margin-bottom: 20px;
        order: -1;
    }

    .productlist .contenedor-filtros {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        width: 90vw;
        margin-top: 40px;
        margin-bottom: 40px;
    }

    .productlist .ruta {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: #8C8C8C;
    
        margin-top: 0px;
        margin-bottom: 0px;
        width: 220px;

        font-size: 14px;
    }
    
    .productlist .filtrar {
        margin-bottom: 0px;
        width: 180px;
    }
    
    .productlist .ordenar {
        margin-bottom: 0px;
        width: 180px;
    }

    .productlist .productos-horizontal {
        display: none;
    }

    .productlist .productos-vertical {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }

    .productlist .productos-vertical-card {
        width: 30%;
    }
}