
.search-container {
    padding: 8px;
    margin: auto;
    
    max-width: 285px;
    border: 1px grey solid;
    border-radius: 3px;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.search-container input {
    border: 0;
    margin-left: 10px;
}




@media (min-width: 700px){
    .search-container {
        display: none;
    }

}

