
.buttonver {
    color: #FFFFFF;
    width: 175px;
    height: 48px;

    border: 2px solid #000000;
    box-shadow: 3px 3px 0px #000000;
    border-radius: 6px;
}

.buttonver-container {
    text-align: center;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 40px;
    display: block;
}


@media (min-width: 700px) {
    .buttonver-container {
        display: none;
    }
}