
.header-desktop {
    display: none;
}


/* Header Mobile */

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5%;
    height: 72px;

    
}

.logo {
    height: 45px;
}

.cart {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart p {
    margin-left: 4px;
    padding-top: 8px;
    color: red;
    font-weight: 700;
}

.header-banner {
    height: 30px;
    background: #7D5A94;

    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    
}

.header-banner p {
    
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 15px;

    letter-spacing: 0.01em;
    font-feature-settings: 'ss01' on, 'case' on;

    color: #FFFFFF;
    padding-left: 10px;
}

.header-banner img {
    z-index: 5;
    margin-right: 5px;
}


/* Header Desktop */

.header-desktop {
    display: none;
}

.header-container .left {
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.header-container .left h3 {
    margin: 10px;
}

.header-container .left ul {
    margin-left: -35px;
    display: flex;
}
.header-container .left ul li {
    font-size: 18px;
    margin: 8px;
    display: inline
}

.header-container .left .hotsale {
    margin: 10px;
}

.header-container .right {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.header-container .right .lupaheader {
    border: 1px solid #8C8C8C;
    border-radius: 50px;
    height: 30px;
    width: 30px;
    /* margin: 15px; */

    display: flex;
    justify-content: center;
    text-align: center;
}

.header-container .right button {
    background: none;
    border: none;
    width: 40px;
    height: 40px;
    
    transition: all 0.5s ease;
    
}

.header-container .right .search {
    transition: all 0.5s ease;
}

.header-container .right .lupaheader img {
    padding: 4px;
    width: 25px;
    height: 25px;
}

.header-container .right .login {
    margin: 15px;
}

.header-container .right .cart {
    margin: 15px;
}

.inactive {
    display: none;
}

.active {
    display: inherit;
}




@media (min-width: 700px){
    .header-mobile {
        display: none;
    }

    .header-desktop {
        display: inline;
    }

    
}