
.pd80px {
    padding-top: 80px;
}

.section-card-conatiner {
    display: flex;
    flex-wrap: wrap;
}

.banner-invierno {
    font-size: 30px;
    font-weight: 600;
    padding: 20px;
    margin: auto;
    display: block;
}

.banner-coleccion {
    display: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
}

.banner-invierno-columns .buttonver-container {
    display: none;
}

.two-columns {
    display: flex;
    width: 100%;
}

.section-card {
    width: 50%;
}

.section-card2 {
    width: 50%;
    padding-top: 80px;
}

.section-card3 {
    display: none;
    height: 50%;
    width: 100%;
}






@media (min-width: 700px) {
    .two-columns {
        width: 49%;
    }

    .banner-invierno-columns {
        order: 1;
        width: 49%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        
        /* justify-content: flex-start;
        align-items: flex-start; */

    }

    .banner-invierno {
        font-size: 2rem;
        font-weight: 600;
        padding: 20px;
        margin-top: 20px;
        margin-bottom: 0px;
        display: block;
       
    }

    .banner-coleccion {
        display: inherit;
        font-size: 1.5rem;
        padding-left: 20px;
        padding-right: 20px;
        margin-top: 0px;
        margin-bottom: 40px;
    }

    .banner-invierno-columns .buttonver-container {
        display: inline-block;
        padding-left: 20px;
        text-align: inherit;
        margin: inherit;
        margin-bottom: 18%;
    }

    

    .section-card3 {
        display: flex;
        width: 100%;
        flex-wrap: nowrap;
    }

    .section-card3 .card-container img {
        width: 100%;
    }
}

@media (min-width: 900px) {
    .banner-invierno {
        font-size: 2rem;
        margin-top: 60px;
       
    }

    .banner-coleccion {
        font-size: 1.1rem;
        margin-bottom: 40px;
    }

    .banner-invierno-columns .buttonver-container {
        margin-bottom: 30%;
    }
}

@media (min-width: 1100px) {
    .banner-invierno {
        font-size: 2.3rem;
        margin-top: 80px;
       
    }

    .banner-coleccion {
        font-size: 1.3rem;
        margin-bottom: 40px;
    }

    .banner-invierno-columns .buttonver-container {
        margin-bottom: 30%;
    }
}

@media (min-width: 1250px) {
    .banner-invierno {
        font-size: 2.5rem;
        margin-top: 80px;
       
    }

    .banner-coleccion {
        font-size: 1.3rem;
        margin-bottom: 40px;
    }

    .banner-invierno-columns .buttonver-container {
        margin-bottom: 30%;
    }
}

@media (min-width: 1350px) {
    .banner-invierno {
        font-size: 2.5rem;
        margin-top: 60px;
       
    }

    .banner-invierno-columns .buttonver-container {
        margin-bottom: 40%;
    }
}