



.card-container-horizontal {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px; 
}

.card-container-horizontal .section1 {
    display: flex;
    width: 50%;
}

.card-container-horizontal .section2 {
    
    width: 50%;
}

.card-container-horizontal .section1 .foto {
    
    width: 100%;
}



.card-names-horizontal {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.card-names-horizontal h1 {
    font-size: 22px;
}

.card-names-horizontal p {
    font-size: 14px;
}

.card-cuotas-horizontal {
    margin-top: 8px;
    
}

.card-precios-horizontal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 165px;
    margin-top: 7px;
    margin-bottom: 20px;
   
}

.card-precios-horizontal h3 {
    margin: 0px;
    font-size: 15px;
    font-weight: 400;
}

.card-precios-horizontal del {
    font-size: 15px;
}

.card-precios-horizontal strong {
    font-size: 15px;
}

.card-cuotas-horizontal h2 {
    margin: 0px;

    /* font-family: 'Inter';
    font-style: normal;
    font-weight: 400; */
    font-size: 12px;
    line-height: 18px;
}

.card-cuotas-horizontal p strong {
    margin: 0px;


    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
}

.card-button-horizontal {
    background-color: #FFFFFF;
    border: 2px solid #76B043;
    border-radius: 6px;

    width: 60px;
    height: 48px;
}


.card-container-horizontal h1 {
    margin: 0px;
}

.card-container-horizontal p {
    margin: 0px;
}

.card-container-horizontal h2 {
    width: 210px;
height: 18px;

/* t1-r */
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 15px;
line-height: 18px;
}





@media (min-width: 300px) {
    .promo10-horizontal {
        align-self: center;
        position: relative;
        left: -8rem;
        bottom: -3rem;
        margin-right: -50px;
        
    }
}

@media (min-width: 450px) {
    .promo10-horizontal {
        align-self: center;
        position: relative;
        left: -13rem;
        bottom: -6rem;
        margin-right: -50px;
        
    }
}
