


.search-container-desktop {
    padding: 8px;
    margin: auto;
    
    max-width: 285px;
    border: 1px grey solid;
    border-radius: 3px;
    display: none;
    justify-content: flex-start;
    align-items: center;
    

}

.search-container-desktop input {
    border: 0;
    margin-left: 10px;
}

@media (min-width: 1000px){

    .search-container-desktop {
        display: flex;
    }
}