
.recorre {
    margin-top: 40px;
    margin-bottom: -110px;
}

.recorre .mochila1 {
    width: 100%;
}

.recorre .mochila2 {
    position: relative;
    width: 50%;
    height: 50%;
    top: -9rem;
    left: 40%;
    
}

.recorre .card-container {
    width: 70%;
    position: relative;
    top: -13rem;
    left: 2rem;
}

.recorre .card-button {
    display: none;
}

.recorre .paginacion {
    position: relative;

    display: flex;
    justify-content: space-between;

    top: -10rem;
}

.recorre .paginacion p {
    margin-left: 45px;
}

.recorre .paginacion .flechas {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.recorre .flecha {
    margin-right: 20px;

    width: 48px;
    height: 48px;
    left: 0px;
    top: calc(50% - 48px/2);
    background: #FFFFFF;
    border: 1px solid #C8C8C8;
    border-radius: 24px;

    display: flex;
    justify-content: center;
    align-items: center;
}





@media (min-width: 700px) {
    .recorre{
        display: flex;
        justify-content: space-around;
        align-items: center;

        margin-top: 40px;
        margin-bottom: -20px;
    }
    .recorre .fotos {
        width: 60%;
    }

    .recorre .cardpaginacion {
        margin-top: -100px;
    }

    .recorre .card-container {
        width: 100%;
        position: inherit;
        
    }

    .recorre .card-button {
        display: inherit;
    }
    
    .recorre .paginacion {
        position: inherit;
    
        display: flex;
        justify-content: space-between;
    
        top: 0rem;
    }
}
