
.juguetesdeldia {
    display: flex;
    flex-direction: column;
}
.juguetesdeldia .titulo {
    text-align: center;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 42px;
    text-align: center;
    letter-spacing: -0.01em;
    font-feature-settings: 'zero' on;
}

.juguetesdeldia .cardfull {
    display: none;
}

.juguetesdeldia .videoplay {
    position: relative;
}

.juguetesdeldia .video {
    display: block;
    width: 90%;
    margin: auto;
}

.juguetesdeldia .play {
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

}





@media (min-width: 700px) {
    .juguetesdeldia .video {
        display: block;
        width: 75%;
        margin: auto;
    }
    
    .juguetesdeldia .play {
        position: absolute;
        z-index: 10;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    
    }

    .videoycard {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    .juguetesdeldia .cardhorizontal {
         display: none;
    }

    .juguetesdeldia .cardfull {
        display: inline;
        width: 25%;
        margin-left: 25px;
    }
    
    

    
}