

.footer {
   margin-top: 30px; 
}

.footer .subir {
   text-align: center; 
}

.footer .contacto {
    display: flex;
    justify-content: space-around;

    margin-top: 15px;
    margin-bottom: 30px;
    

}

.footer .contacto .left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    
    width: 40%;
    
}

.footer .contacto .left h4 {
    margin-bottom: 5px;
    padding-right: 15px;
}

.footer .contacto .left h5 {
    margin-top: 5px;
    padding-right: 15px;

    font-style: normal;
    font-weight: 900;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
}

.footer .contacto .right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 50%;
    
}

.footer .contacto .right h5 {
    margin-bottom: 10px;

    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 10px;
    line-height: 12px;
}

.footer .contacto .right form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.footer .contacto .right input {
    width: 130px;
    margin-top: 0px;
    margin-bottom: 8px;
}

.footer .contacto .right button {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 9px 12px;

    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-style: normal;
    font-weight: 800;
    font-size: 10px;
    line-height: 12px;

    width: 70px;
    height: 30px;
    left: 166px;
    top: 90px;

    background: #FFFFFF;
    border: 2px solid #000000;
    box-shadow: 3px 3px 0px #000000;
    border-radius: 6px;
}

.footer .sections {
    display: flex;
    justify-content: center;
    width: 100%;
}

.footer .sections .left {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-right: 15px;


}

.footer .sections .center {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    
}

.footer .sections .right {
    display: flex;
    flex-direction: column;
    padding-left: 25px;
   
    
}

.footer .sections p {
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-weight: 800;
    font-size: 10px;
    line-height: 12px;
}

.footer .logos {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin-top: 30px;
    margin-bottom: 40px;
}

.logos img {
    max-width: 48px;
    max-height: 36px;

    margin-right: 10px;
    margin-left: 10px;
}

.footer .logos .superior .separador {
    width: 2px;
}

.footer .logos .superior {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer .logos .inferior {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 15px;
}







@media (min-width: 700px) {
   

    

    .footer .contacto {
        display: none;
    }

    

    .footer .logos {
        display: none;
    }

    .footer .sections {
        margin-top: 50px;
        margin-bottom: 50px;
    }

}