
.card-container {
    display: flex;
    flex-direction: column;
    margin: 15px;
    margin-top: 30px;
    
}

.promo10 {
    position: relative;
    height: 70px;
    width: 70px;
    top: -4.5rem;
    left: 1.5rem;
    margin-bottom: -70px;
}

.card-names {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.card-names h1 {
    font-size: 1.1rem;
}

.card-names p {
    font-size: 0.8rem;
    text-overflow: ellipsis;
    overflow: hidden;

    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    white-space: normal;

}

.card-cuotas {
    margin-top: 0.1rem;
    
}

.card-cuotas p strong {
    margin: 0px;


    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    
    
}

.card-precios {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
    /* max-width: 165px; */
    margin-top: 5px;
    margin-bottom: 20px;
    margin-right: 15px;
   
}

.card-precios h3 {
    margin: 0px;
    font-size: 0.9rem;
    font-weight: 400;
    
}

.card-precios del {
    margin-left: 4px;
    font-size: 0.8rem;
    
}

.card-precios strong {
    margin-left: 5px;
    font-size: 0.8rem;
    
}

.card-cuotas h2 {
    margin-top: 3px;
    margin-bottom: 0px;
    font-size: 12px;
    line-height: 18px;
    font-family: 'Inter';
font-style: normal;
font-weight: 400;
}



.card-button {
    background-color: #FFFFFF;
    border: 2px solid #76B043;
    border-radius: 6px;

    width: 60px;
    height: 48px;

    display: none;
}


.card-container h1 {
    margin: 0px;
}

.card-container p {
    margin: 0px;
}








@media (min-width: 700px) {
    .card-button {
        display: inherit;
    }
}